.v-box {
	padding: 20px;
	&__container {
		margin: 0 auto;
		max-width: 400px;
		font-size: 14px;
		text-align: center; }
	&__icon {
		margin-bottom: 20px;
		font-size: 0;
		.fa {
			color: $green;
			font-size: 70px; } }
	&__title {
		color: #222;
		font-size: 20px; }
	&__description {
		margin-top: 5px;
		p {
			padding: 0;
			margin: 0;
			font-size: 15px;
			&:not(:last-child) {
				margin-bottom: 1em; } } }
	&__form {
		padding: 30px 0;
		text-align: left; }
	&__field {
		position: relative;
		margin-bottom: 20px; }
	&__row {
		position: relative;
		margin: 0 -10px;
		@include clr; }
	&__col {
		padding: 0 10px;
		&_md {
			float: left;
			width: 50%;
			@include r(400) {
				width: 100%; } } }
	&__label {
		margin-bottom: 10px; }
	&__submit {
		text-align: center; }
	&__hint {
		@include r(900) {
			position: static;
			clear: both;
			width: auto;
			margin: 0 10px 20px;
			@include gpu; }
		&:before,
		&:after {
			@include r(900) {
				display: none; } } } }
