.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

* {
  font-family: 'Open Sans', Helvetica, sans-serif;
}

html,
body {
  height: 100%;
}

body {
  color: #666;
  background: #fff;
  line-height: 1.2;
}

a {
  color: #2e99db;
  text-decoration: none;
}

a:hover {
  color: #70bfed;
  text-decoration: underline;
}

[hidden] {
  display: none !important;
}

.v-label {
  display: inline-block;
  vertical-align: top;
}

.v-label__required {
  color: #2e99db;
  padding-left: 0.35em;
}

.v-btn {
  display: inline-block;
  vertical-align: top;
  padding: 16px 20px;
  margin: 0;
  border-bottom: 1px solid #0c77b9;
  border-radius: 2px;
  min-width: 142px;
  color: #fff;
  background: #2e99db;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  transition: background 0.3s, border-color 0.3s;
}

.v-btn:hover {
  color: #fff;
  border-color: #509dcb;
  background: #70bfed;
}

.v-btn_wide {
  width: 100%;
}

.v-btn_sm {
  padding: 14px 20px 13px;
  min-width: 100px;
}

.v-select {
  display: block;
  padding: 13px 35px 13px 13px;
  margin: 0;
  border: 1px solid #e1e1e1;
  border-radius: 2px;
  width: 100%;
  height: 44px;
  color: #595959;
  background-image: url("data:image/png;base64,");
  background-position: right center;
  background-repeat: no-repeat;
  background-color: #fff;
  font-family: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  -webkit-appearance: none;
}

.v-select::-ms-expand {
  display: none;
}

.v-input {
  display: block;
  padding: 13px;
  margin: 0;
  border: 1px solid #e1e1e1;
  border-radius: 2px;
  width: 100%;
  height: 44px;
  color: #595959;
  background: #fff;
  font-family: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: normal;
  appearance: none;
}

.v-input.is-error {
  border-color: #ec4a41;
}

.v-input.is-success {
  border-color: #31b73b;
}

.v-form-hint {
  position: absolute;
  top: 50%;
  right: 100%;
  display: none;
  padding: 10px 0;
  border: 1px solid #eee;
  width: 200px;
  background: #fdfdfd;
  transform: translate3d(-20px, -50%, 0);
}

.v-form-hint.is-open {
  display: block;
}

.v-form-hint:before, .v-form-hint:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 100%;
}

.v-form-hint:before {
  margin-top: -12px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 12px 0 12px 12px;
  border-color: transparent transparent transparent #eee;
}

.v-form-hint:after {
  margin-top: -11px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 11px 0 11px 10px;
  border-color: transparent transparent transparent #fdfdfd;
}

.v-form-hint__item {
  position: relative;
  padding: 2px 10px 2px 40px;
  margin: 0;
  color: #ec4a41;
  font-size: 14px;
  line-height: 16px;
  list-style-type: none;
}

.v-form-hint__item.is-success {
  color: #31b73b;
}

.v-form-hint__item.is-success .form-hint__close {
  display: none;
}

.v-form-hint__item.is-success .form-hint__check {
  display: inline-block;
}

.v-form-hint__icon {
  position: absolute;
  top: 1px;
  left: 0;
  width: 40px;
  font-size: 0;
  line-height: 1;
  text-align: center;
}

.v-form-hint__close, .v-form-hint__check {
  display: inline-block;
  vertical-align: top;
  font-size: 18px;
}

.v-form-hint__check {
  display: none;
}

.v-box {
  padding: 20px;
}

.v-box__container {
  margin: 0 auto;
  max-width: 400px;
  font-size: 14px;
  text-align: center;
}

.v-box__icon {
  margin-bottom: 20px;
  font-size: 0;
}

.v-box__icon .fa {
  color: #31b73b;
  font-size: 70px;
}

.v-box__title {
  color: #222;
  font-size: 20px;
}

.v-box__description {
  margin-top: 5px;
}

.v-box__description p {
  padding: 0;
  margin: 0;
  font-size: 15px;
}

.v-box__description p:not(:last-child) {
  margin-bottom: 1em;
}

.v-box__form {
  padding: 30px 0;
  text-align: left;
}

.v-box__field {
  position: relative;
  margin-bottom: 20px;
}

.v-box__row {
  position: relative;
  margin: 0 -10px;
}

.v-box__row:after {
  content: " ";
  display: table;
  clear: both;
}

.v-box__col {
  padding: 0 10px;
}

.v-box__col_md {
  float: left;
  width: 50%;
}

@media only screen and (max-width: 400px) {
  .v-box__col_md {
    width: 100%;
  }
}

.v-box__label {
  margin-bottom: 10px;
}

.v-box__submit {
  text-align: center;
}

@media only screen and (max-width: 900px) {
  .v-box__hint {
    position: static;
    clear: both;
    width: auto;
    margin: 0 10px 20px;
    transform: translate3d(0, 0, 0);
  }
}

@media only screen and (max-width: 900px) {
  .v-box__hint:before, .v-box__hint:after {
    display: none;
  }
}

[data-animation="icon"] {
  opacity: 0;
  animation: fade-top 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.225s forwards;
}

[data-animation="title"] {
  opacity: 0;
  animation: fade-top 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.2s forwards;
}

[data-animation="text"] {
  opacity: 0;
  animation: fade-top 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.15s forwards;
}

[data-animation="form"] {
  opacity: 0;
  animation: fade 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.2s forwards;
}

[data-animation="footnote"] {
  opacity: 0;
  animation: fade-bottom 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.2s forwards;
}

@keyframes fade-top {
  0% {
    opacity: 0;
    transform: translate3d(0, -50px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fade-bottom {
  0% {
    opacity: 0;
    transform: translate3d(0, 50px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

