.spinner {
  position: relative;
  margin: 0 auto;
  padding: 0 52px;
  width: 180px;
}

.spinner__minus, .spinner__plus {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: 0;
  border: none;
  width: 53px;
  color: #fff;
  background: #2e99db;
}

.spinner__minus:hover, .spinner__plus:hover {
  background: #1B79B2;
}

.spinner__minus:active, .spinner__plus:active {
  background: #286090;
}

.spinner__minus:disabled, .spinner__plus:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.spinner__minus:before, .spinner__minus:after, .spinner__plus:before, .spinner__plus:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 1px;
  background: #fff;
}

.spinner__minus:before, .spinner__plus:before {
  margin: -1px 0 0 -10px;
  width: 20px;
  height: 2px;
}

.spinner__minus {
  left: 0;
  border-radius: 3px 0 0 3px;
}

.spinner__minus:after {
  display: none;
}

.spinner__plus {
  right: 0;
  border-radius: 0 3px 3px 0;
}

.spinner__plus:after {
  margin: -10px 0 0 -1px;
  width: 2px;
  height: 20px;
}

.spinner__input {
  display: block;
  padding: 0 10px;
  margin: 0 auto;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  border-radius: 0;
  width: 100%;
  height: 53px;
  background: #fff;
  font-size: 18px;
  line-height: normal;
  text-align: center;
}

.spinner__input:focus {
  border-color: #2e99db;
}

